<template>
    <div class="app-wrap">
      <div class="share-tip" v-if="page.pageInfo.subscribe === '0' || !page.pageInfo.subscribe">
        <div class="code">
          <div class="code-box">
            <div class="code-tip">
              <p>请长按下图选择[识别图中二维码]</p>
              <p>关注并进入公众号后，找到底部菜单</p>
              <p>参与活动</p>
            </div>
            <div class="code-icon"><img src="http://mmbiz.qpic.cn/mmbiz_jpg/SR7QeqmaJLw0ysgiaPTsDKhQRgOQo4LQgx1oMn5GKpqqean5jTWvy58wecwIOsgNoHVjPaJ51IyBejB1gQPeXuA/0"></div>
          </div>
        </div>
      </div>
        <div class="app-box" v-else>
          <modal :title="page.title[page.index]" :open="modalVisible">
            <div slot="modal-content">
              <!-- register -->
              <div class="modal-box" v-show="manageModal[0]">
                <div class="reg-item">
                  <label class="reg-name"></label>
                  <input type="text" maxlength="9" placeholder="姓名"v-model="page.params.name">
                </div>
                <div class="reg-item">
                  <label class="reg-tel"></label>
                  <input type="tel" maxlength="11" placeholder="手机号" v-model="page.params.phone">
                </div>
                <div class="reg-tips">(领取奖品使用)</div>
                <button type="button" class="modal-btn" @click.prevent.stop="subLogin">立即提交</button>
              </div>
              <!-- register-end -->
              <!-- rule -->
              <div class="modal-box rule-box" v-show="manageModal[1]">
                <div class="rule-text">
                  <p>1、微信用户关注“武进雪堰”微信公众号，填写姓名和手机号码后，方可进入答题。</p>
                  <p>2、每次答题共有十道题目，题目从题库中随机抽取，每题分值为10分，答题时间10秒，答对得分，答错不得分，答题用时越短所获分值越高，总分为十题分值相加，根据成绩建立排行榜，取前十名公布。</p>
                  <p>3、题目显示方式为逐个显示，答一题换一题，不可回看。</p>
                  <p>4、活动分为三期，时间为2018年3月21日—23日，每天的13:30-20:30。每个微信号参与一次，分享至朋友圈，可多参与一次，同一微信号取成绩最好一次记录。</p>
                  <p>5、每期排行榜前十名可获价值150元左右雪堰农副产品，三期获奖人员共计30人，每期获奖名单将在次日上午武进雪堰微信公众号公布。每个微信号只获奖一次。</p>
                  <p>6、领奖方式：2018年3月26日至30日，到雪堰镇便民服务中心（潘北街1号）领取。时间：上午8:00至11:00，下午1:00至4:30,凭获奖者手机号码领取。</p>
                </div>
                <div class="modal-btn-gp">
                  <button type="button" class="rule-left-btn" @click.prevent.stop="openModal(2)">奖品设置</button>
                  <button type="button" class="rule-right-btn" @click.prevent.stop="openModal(3, 1)">开始答题</button>
                </div>
              </div>
              <!--rule-end-->
              <!--prize-->
              <div class="modal-box prize-box" v-show="manageModal[2]">
                <div class="prize-item">
                  <h4>每期排行榜前十名</h4>
                  <p><span>可获价值150元左右的雪堰农副产品</span></p>
                </div>
                <div class="modal-btn-gp">
                  <button type="button" class="rule-left-btn" @click.prevent.stop="openModal(1)">活动规则</button>
                  <button type="button" class="rule-right-btn" @click.prevent.stop="openModal(3, 1)">开始答题</button>
                </div>
              </div>
              <!--prize-end-->
              <!--exam-->
              <div class="modal-box exam-box" v-if="page.qa" v-show="manageModal[3]">
                <div class="exam-header">
                  <span>得分：{{page.current_score}} 分</span>
                  <div class="exam-progress">已答 <span>{{page.e_index}} / 10</span></div>
                </div>
                <div class="exam-question">
                  {{page.qa.name}}
                </div>
                <div class="exam-content fail">
                  <div v-for="(data, index) in page.qa.answers" :class="'exam-item ' + ((page.qa.rights === data.aId.toString()) ? 'right': ((page.chosedIndex == index.toString()) ? 'error' : ''))"  @click.prevent.stop="page.isAns || ansQa({
                aId: data.aId,
                qId: page.qa.qId
                }, index)">{{index + 1}}. {{data.name}}</div>
                </div>
                <button type="button" class="modal-btn" @click.prevent.stop="requireQa(page.isAns, page.qa.next_qId)">{{!page.qa.next_qId && page.isAns ? '查看成绩' : '下一题'}}</button>
                <div class="exam-tip" v-show="parseInt(page.hitNumber) > 0 && page.tipVis">{{page.hitNumber}}连击!</div>
              </div>
              <!--exam-end-->
              <!--my-score-->
              <div class="modal-box score-box" v-show="manageModal[4]" v-if="page.scoreInfo">
                <div class="score-item">
                  <span>本轮答题得分: </span><span>{{page.current_score}} 分</span>
                </div>
                <div class="score-item">
                  <span>历史最高得分: </span><span>{{page.scoreInfo.todayScore}} 分</span>
                </div>
                <div class="score-item">
                  <span>当前答题排名: </span><span>{{page.scoreInfo.myRank}} 名</span>
                </div>
                <div class="score-item">
                  <span>距上一名还差: </span><span>{{page.scoreInfo.nextScore.nextScore ? page.scoreInfo.nextScore.nextScore: 0}} 分</span>
                </div>
                <button type="button" class="modal-btn" @click.prevent.stop="tryAgain">再答一次</button>
              </div>
              <!--my-score-end-->
              <!--my-rank-->
              <div class="modal-box rank-box" v-show="manageModal[5]" v-if="page.ranklist">
                <div class="rank-item" v-for="data in page.ranklist">
                  <span>第 {{data.rank}} 名</span>
                  <span>{{data.nickName}}</span>
                  <span>{{data.scores}} 分</span>
                </div>
                <button type="button" class="modal-btn" @click.prevent.stop="tryAgain">再答一次</button>
              </div>
              <!--my-rank-end-->
            </div>
          </modal>
          <div class="app-btnGp" v-show="!modalVisible">
            <div class="begin-btn" @click.prevent.stop="openModal(3, 1)"></div>
            <div class="app-footbar">
              <div class="rule-btn" @click.prevent.stop="openModal(1)"></div>
              <div class="prize-btn" @click.prevent.stop="openModal(2)"></div>
            </div>
          </div>
          <div class="app-btnTop" v-show="modalVisible">
            <div class="nick-box" v-if="page.scoreInfo">
              <img :src="page.pageInfo.headImgUrl">
              <p>{{page.pageInfo.nickname.substr(0, 4)}}..</p>
            </div>
            <div class="rank-wrapbox" @click.prevent.stop="openModal(5, 2)" v-if="page.scoreInfo">
              <div class="rank-icon"></div>
              <p>排行榜</p>
            </div>
          </div>
        </div>
        <div class="share-tip" v-show="page.shareVisible">
          <div class="share-banner"></div>
        </div>
        <loading></loading>
      </div>
</template>

<script type="text/ecmascript-6">
  import modal from './components/modal';
  import loading from './components/loadingView';
  import {account_register, require_user, require_question, answer_question, require_rank, require_score, shareApi, addTimes} from './mock/mock';
    export default {
      name: "app",
      data () {
        return {
          modalVisible: !1,
          page: {
            index: -1,
            params: {
              openId: 'test',
              name: null,
              phone: null,
              nickName: null
            },
            title: ['报名信息', '活动规则', '奖品设置', '知识竞赛', '我的成绩', '排行榜'],
            modalVisibleArr: [!1, !1, !1, !1, !1, !1],
            e_index: 0,
            qa: null,
            qa_time: null,
            isAns: !1,
            tipVis: !1,
            chosedIndex: null,
            current_score: 0,
            ranklist: null,
            scoreInfo: null,
            userId: window.localStorage.getItem('userId') ? window.localStorage.getItem('userId') : null,
            joinTimes: 0,
            intime: null,
            shareVisible: !1,
            pageInfo: {},
            hitNumber: 0
          }
        };
      },
      created () {
        this.init();
        this.page.userId || (this.modalVisible = !0, this.page.index = 0, this.page.modalVisibleArr[this.page.index] = !0);
        this.page.userId && this.checkUser();
        this.shareInit();
      },
      computed: {
        manageModal () {
          this.page.modalVisibleArr[this.page.index] && (this.page.modalVisibleArr[this.page.index] = !0);
          return this.page.modalVisibleArr;
        }
      },
      methods: {
        init () {
          this.page.pageInfo['nickname'] = window.decodeURI(this.getParams('nickName'));
          this.page.pageInfo['headImgUrl'] = window.decodeURIComponent(this.getParams('headImgUrl'));
          this.page.pageInfo['openId'] = window.decodeURI(this.getParams('openId'));
          this.page.pageInfo['subscribe'] = window.decodeURI(this.getParams('subscribe'));
          this.page.pageInfo['ticket'] = window.decodeURI(this.getParams('ticket'));
        },
        async checkUser () {
          try {
            let res = await require_user({userId: this.page.userId});
            this.getJoinTimes();
          } catch (e) {
            window.localStorage.removeItem('userId');
            this.modalVisible = !0;
            this.page.index = 0;
            this.page.modalVisibleArr[this.page.index] = !0;
          }
        },
        getParams (key) {
          let url = location.search.replace(/^\?/, '').split('&');
          let paramsObj = {};
          for (let i = 0, iLen = url.length; i < iLen; i++) {
            let param = url[i].split('=');
            paramsObj[param[0]] = param[1];
          }
          if (key) {
            return paramsObj[key] || '';
          }
          return paramsObj;
        },
        openModal (index, v) {
          if (v === 1) {
            console.log(this.page.intime);
            if (this.page.intime) {
              alert('不在活动时间内！');
              return;
            }
            if (!this.page.joinTimes) {
              this.share();
              return;
            }
            this.requireQa(!0, 0);
          };
          v === 2 && this.rankList();
          this.page.modalVisibleArr = [!1, !1, !1, !1, !1, !1];
          this.modalVisible = !0;
          this.page.index = index;
          this.page.modalVisibleArr[this.page.index] = !0;
        },
        requireQa (v, qId) {
          v || alert('请先作答！');
          if (v) {
            qId === '' ? this.getMyscore().then(() => {
                this.openModal(4);
              }) : this.getQuestion(qId).then(() => {
              this.page.tipVis = !1;
              this.page.e_index++;
              this.page.chosedIndex = null;
              this.page.isAns = !1;
              this.page.qa && (this.page.qa_time = (new Date()).getTime());
            });
          }
        },
        async subLogin () {
          try {
            this.page.params.openId = this.page.pageInfo.openId;
            this.page.params.name = this.page.params.name.toString();
            this.page.params.phone = this.page.params.phone.toString();
            let checkMobile=/^(13[0-9]|15[0|1|2|3|5|6|7|8|9]|18[0|1|2|5|6|7|8|9]|147|177)\d{8}$/;
            this.page.params.nickName = this.page.pageInfo.nickname;
            if (!checkMobile.test(this.page.params.phone) || !this.page.params.name.trim().length || !this.page.params.phone.trim().length) {
              alert('注册填写错误！');
            } else {
              let res = await account_register(this.page.params);
              res.data.userId && window.localStorage.setItem('userId', res.data.userId);
              this.page.userId = res.data.userId;
              this.page.index = 1;
              this.modalVisible = !1;
              this.getJoinTimes();
            }
          } catch (e) {
            alert('网络连接错误，请重试');
          }
        },
        async getQuestion (qId) {
          try {
            let res = await require_question({qId: qId, userId: this.page.userId});
            this.page.qa = res.data;
          } catch (e) {
            console.log(e);
            this.page.qa = null;
            this.getMyscore().then(() => {
              this.openModal(4);
              alert('答题机会已用完，请分享后再试');
            });
          }
        },
        async ansQa (params, index) {
          this.page.chosedIndex = index;
          params['num'] = this.page.e_index;
          params['hitNum'] = this.page.qa.hitNum ? this.page.qa.hitNum: 0;
          params['userId'] = this.page.userId;
          let wasteTime = Math.round(((new Date()).getTime() - this.page.qa_time) / 1000);
          params['leftTime'] = (15 - wasteTime > 0) ? (15 - wasteTime) : 0;
          try {
            let res = await answer_question(params);
            this.page.isAns = !0;
            this.page.current_score = res.data.score;
            this.$set(this.page.qa, 'hitNum', res.data.hitNum);
            this.$set(this.page.qa, 'rights', res.data.rights);
            this.page.hitNumber = parseInt(res.data.hitNum) === 1 ? (this.page.hitNumber + 1) : 0;
            this.page.tipVis = !0;
          } catch (e) {
            console.log(e);
            alert('请求失败，请重试！');
          }
        },
         async rankList () {
           try {
             let params = {
               userId: this.page.userId,
               rows: 10,
               time: (new Date().getFullYear() + '-' + (new Date().getMonth() + 1) + '-' + new Date().getDate()).toString()
             }
             let res = await require_rank(params);
             this.page.ranklist = res.data.list;
           } catch (e) {
             console.log(e);
           }
         },
        async getMyscore () {
          try {
            let res = await require_score({
              userId: this.page.userId,
              _timestamp: new Date().getTime()
            });
            this.page.scoreInfo = res.data;
            this.page.joinTimes = res.data.num;
          } catch (e) {
            console.log(e);
          }
        },
        async getJoinTimes () {
          let res = await require_score({
            userId: this.page.userId,
            _timestamp: new Date().getTime()
          });
          this.page.joinTimes = res.data.num;
          this.page.intime = res.data.info.length > 0;
        },
        share () {
            this.page.shareVisible = !0;
        },
        async shareInit () {
          try {
            let res = await shareApi({
              ticket: this.page.pageInfo.ticket,
              url: location.href.split('#')[0]
            });
            wx.config({
              debug: false,
              appId: res.data.appId,
              timestamp: res.data.timestamp,
              nonceStr: res.data.nonceStr,
              signature: res.data.signature,
              jsApiList: ['onMenuShareTimeline','onMenuShareAppMessage','hideMenuItems']
            });
            wx.ready(() => {
              wx.hideMenuItems({
                menuList: ['menuItem:copyUrl',
                  'menuItem:share:qq',
                  'menuItem:share:weiboApp',
                  'menuItem:share:facebook',
                  'menuItem:share:QZone',
                  'menuItem:originPage',
                  'menuItem:openWithQQBrowser',
                  'menuItem:openWithSafari',
                  'menuItem:share:email'
                ]
              });
              wx.error( (res) => {
                console.log("验证失败"+res);
              });
              wx.onMenuShareTimeline({
                title: '雪堰桃花节知识竞赛',
                link: 'http://www.me2u.com.cn/weixinApi/oauth/login?code=wxc49375f2cee5ffe0&returnUrl=http://www.me2u.com.cn/PeachFlowerFv/',
                imgUrl: "http://www.me2u.com.cn/yzdtp/resources/active/xy_qa_head.jpg",
                success:  () => {
                  alert('分享成功！');
                  this.unlocked();
                }
              });
            });
          } catch (e) {
            alert('初始化失败，请刷新重试！');
          }
        },
        async unlocked () {
          try {
            let res = await addTimes({
              userId: this.page.userId
            });
            this.page.joinTimes = res.data.num;
            this.page.shareVisible = !1;
          } catch (e) {
            console.log(e);
          }
        },
        tryAgain () {
          this.page.e_index = 0;
          this.page.hitNumber = 0;
          if (!this.page.joinTimes) {
            this.share();
            return;
          }
          this.openModal(3, 1);
        }
      },
      components: {
        modal,
        loading
      }
    };
</script>

