              box-sizing border-box
            & p
              text-align left
          .rank-wrapbox
            position absolute
            right rpx(60)
            top rpx(10)
          & p
            font-size rpx(26)
            color #fff
            -webkit-text-stroke rpx(2) #673400
            font-weight bold
            height rpx(40)
            line-height rpx(40)
            text-align center
          & img, .rank-icon
            display block
            width rpx(106)
            height rpx(106)
            border-radius 50%
            overflow hidden
          .rank-icon
            background url("assets/img/sort_icon.png") no-repeat
            background-size 100% 100%
      .share-tip
        position absolute
        z-index 99999
        width 100%
        height 100%
        background-color rgba(0,0,0,0.7)
        top 0
        left 0
        display flex
        justify-content flex-end
        .share-banner
          width rpx(347)
          height rpx(187)
          background url("assets/img/share.png") no-repeat
          background-size 100% 100%
        .code
          width 100%
          height 100%
          display flex
          justify-content center
          align-items center
          .code-box
            .code-icon
              width rpx(400)
              height rpx(400)
              margin 0 auto
              & img
                display block
                width 100%
                height 100%
            .code-tip
              font-size rpx(24)
              color #fff
              text-align center
              padding rpx(20) 0
              & p
                line-height rpx(40)
</style>



