import Axios from 'axios';

const Api = process.env.NODE_ENV === 'development' ? '/api' : 'http://www.me2u.com.cn/yzd/api';
const Wx = process.env.NODE_ENV === 'development' ? '/wxshare': 'http://www.me2u.com.cn/wxshare';


const getMock = (url) => {
  return (params) => Axios.get(url, {
    params,
    timeout: 8000
  });
};

const postMock = (url) => {
  return (params) => Axios.post(url, params, {
    timeout: 8000
  })
};

const account_register = getMock(Api + '/login');
const require_user = getMock(Api + '/userInfo');
const require_question = getMock(Api + '/questionsNext');
const answer_question = getMock(Api + '/answer');
const require_rank = getMock(Api + '/rankList');
const require_score = getMock(Api + '/myScore');
const shareApi = getMock(Wx + '/share/xy');
const addTimes = getMock(Api + '/addTimes');

export {
  account_register,
  require_user,
  require_question,
  answer_question,
  require_rank,
  require_score,
  shareApi,
  addTimes
};



